                            "new":"./img/home/about_news_7.png",
                            "new_word":"在日本音乐界中拥有着超高人气的艺术家米津玄师。同时，在中国国内也被粉丝们交口称誉，并爱称他为“八爷”。本次米津玄师发表了与优衣库的T恤独立品牌「UT」的首次联动。继上周在美国，加拿大的先行贩售之后，新商品也决定于8月14日开始，在全球25市场的优衣库店铺进行贩售。",
                            "new_date":"2020.09.24",
                            "new_write":"喵大",
                            "new_real_word":"在日本音乐界中拥有着超高人气的艺术家米津玄师。同时，在中国国内也被粉丝们交口称誉，并爱称他为“八爷”。本次米津玄师发表了与优衣库的T恤独立品牌「UT」的首次联动。继上周在美国，加拿大的先行贩售之后，新商品也决定于8月14日开始，在全球25市场的优衣库店铺进行贩售。数日前在美国-纽约第五大道突然出现了，使用米津玄师5th album「STRAY SHEEP」的主打插画作为主题的联动装潢店铺。作为本次全球贩卖的始发阵容，8月7日在北美进行的先行贩卖也成为了巨大的话题。本次的联动装潢店铺将在美国／中国／日本，全球合计8店铺实施。中国国内实施店铺为上海店以及广州维多利亚广场店。米津玄师身为音乐家，同时也是一名插画家，在绘制CD封面、周边等方面也十分出名。在本次联动登场的作品中，有使用了米津的代表插画形象之一的猫和各种原创角色的创作，也有大胆地直接使用插画和幻想氛围的照片组合来表现的作品等等。T恤的底色也不仅限于黑白，还追加了蓝色与粉色来满足所有人的需求。与此同时，米津玄师的最新专辑「STRAY SHEEP」也于8月5日开始正在发售中。Billboard JAPAN Top Albums Sales榜单中达成了CD发售以来一周时间内达到了88万张销量的记录。与本消息一同公开的还有将一直在上海外滩的震旦大屏上播放的宣传影像，米津的存在感也将更大地扩散开去。希望各位也能持续关注。"
