                            "id":8,
                            "title":"直击2020 ChinaJoy育碧发布会现场漫画《刺客信条：王朝》正式公布",
                            "new":"./img/home/about_news_8.png",
                            "new_word":"直击2020 ChinaJoy育碧发布会现场 首部中国原创漫画《刺客信条：王朝》正式公布，此次“育碧”携手国内精品漫画平台“新漫画”，共同打造刺客信条系列首部中国原创漫画《刺客信条：王朝》。该作品由新漫画旗下《镖人》作者许先哲和漫画家张肖共同创作，旨在通过全新的形式，在带给玩家新鲜体验的同时，将中国文化推向世界。",
                            "new_date":"2020.09.24",
                            "new_write":"喵小二",
                            "new_real_word":"近日，育碧首部中国原创漫画《刺客信条：王朝》，首次亮相2020 ChinaJoy育碧舞台。作为世界知名游戏IP，《刺客信条》系列涉猎世界多国文化，为世界各地玩家创造了印象深刻的游戏体验。此次“育碧”携手国内精品漫画平台“新漫画”，共同打造刺客信条系列首部中国原创漫画《刺客信条：王朝》。该作品由新漫画旗下《镖人》作者许先哲和漫画家张肖共同创作，旨在通过全新的形式，在带给玩家新鲜体验的同时，将中国文化推向世界。2020 ChinaJoy《刺客信条：王朝》发布现场新漫画CEO朱槿于发布会现场表示：“新漫画一直希望打造出能在世界舞台发光的中国漫画作品，我们也一直朝着这个方向不断努力。十分开心育碧能够邀请我们一同合作这次《刺客信条》的项目，在合作上我们可以算得上是一拍即合。但其实大家都知道，达成共识容易，创作本身却不容易。如何保留《刺客型条》本身精髓的同时，更好地将中国历史展现在漫画中，这需要找到一个切入点。我们从方方面面研究了中国几千年的历史，最终发现了“侠文化”与《刺客信条》存在一定的契合度。”《刺客信条》品牌经理郁欣明则表示：“很开心能与新漫画一起，将中国漫画推向世界。让全世界玩家都能对中国有更多的了解。我们希望《刺客信条：王朝》漫画，不仅仅以漫画形式，也能以其他形式展现，带给玩家更多的体验。”来自唐朝天宝年间的刺客李萼究竟会给我们带来什么样的故事？就让我们敬请期待吧！"
