                            "new_word":"在今天（3月8日）早些时候，富士电视台的动画新作品发布会 Plus Ultra富士电视台2018动画系列发表会~上发表了 Niantic Labs 的LBS类型游戏 《Ingress》的动画化消息。同时 Niantic 也公布了动画化作品的官方网站 ingress-anime.com ，作品预计于今年10月份上映。",
                            "new_date":"2020.09.24",
                            "new_write":"喵大",
                            "new_real_word":"在今天（3月8日）早些时候，富士电视台的动画新作品发布会,Plus Ultra ~富士电视台2018动画系列发表会~ 上发表了 Niantic Labs 的LBS类型游戏 《Ingress》的动画化消息。同时 Niantic 也公布了动画化作品的官方网站 ingress-anime.com ，作品预计于今年10月份上映。《Ingress》动画化作品将在与现实相近的时间线上展开，在本片放送期间《Ingress》的游戏本体上也会与放送中的动画进行联动。同时，动画版《Ingress》也计划将AR（增强现实）运用到作品中，实现动画、游戏、现实世界的“三元素作品(the three elements of animation)”。从官网信息了解到，本作品由 Niantic Labs 的老朋友 CraftAR 制作，由櫻木優平担任监督。櫻木優平监督是CG监督出身， CraftAR 公司本身原来也是以CG外包为主要业务。同时，CraftAR也曾经负责过《Pokemon GO》的人物设计和建模。相信两家的良好合作能给新老玩家带来高质量的动画。虽然看上去很高端，但是很多玩家看到该消息的第一反应是“啊？这怎么动画化啊？”部分老玩家还是对猩猩（开发商Niantic Labs的外号）的这一举动不太理解。并且此前猩猩的工作重心一直在Pokemon Go上，甚至有一些“利用和抛弃老玩家”的行为，继发布《Ingress Prime》后本次继续用动画化给Ingress宣传造势，看上去猩猩有“捡回Ingress发扬光大”的意思。"
                        },
